import {window, document} from "./browser";

const utils = {
	/**
	 * Select or create element
	 * @param {String|HTMLElement} param
	 *  when string given is as HTML tag, then create element
	 *  otherwise it returns selected elements
	 * @returns {HTMLElement}
	 */
	$(param) {
		let el = null;

		if (typeof param === "string") {
			// check if string is HTML tag format
			const match = param.match(/^<([a-z]+)\s*([^>]*)>/);

			// creating element
			if (match) {
				el = document.createElement(match[1]);

				// attributes
				match.length === 3 &&
					match[2].split(" ").forEach(v => {
						const attr = v.split("=");

						el.setAttribute(attr[0], attr[1]);
					});
			} else {
				el = document.querySelectorAll(param);
				el = el.length === 1 ? el[0] : el;
			}
		} else if (param.nodeName && param.nodeType === 1) {
			el = param;
		}

		return el;
	},

	/**
	 * Check if is object
	 * @param {Object} obj
	 * @returns {Boolean}
	 */
	isObject(obj) {
		return typeof obj === "object" &&
			!Array.isArray(obj) && obj !== null;
	},

	/**
	 * Merge object
	 * @param {Object} target
	 * @param {Object} source
	 * @returns {Object} merged target object
	 * @example
	 *  var target = { a: 1 };
	 *  c3p.extend(target, { b: 2, c: 3 });
	 *  target;  // { a: 1, b: 2, c: 3 };
	 */
	extend(target, source) {
		if (this.isObject(target) && this.isObject(source)) {
			for (const key in source) {
				if (this.isObject(source[key])) {
					!target[key] && Object.assign(target, {[key]: {}});
					this.extend(target[key], source[key]);
				} else {
					Object.assign(target, {[key]: source[key]});
				}
			}
		}
		return target;
	},

	/**
	 * Get the style value or apply
	 * @param {HTMLElement} el
	 * @param {String|Object} style
	 *  String: return style property value
	 *  Object: set style value
	 * @parma {Boolean} getAsNumber - get the value as number
	 * @returns {String|HTMLElement}
	 */
	css(el, style, getAsNumber) {
		if (typeof(style) === "string") {
			const value = getComputedStyle(el)[style];

			return getAsNumber ? this.getNumValue(value) : value;
		} else {
			Array.isArray(el) ?
				el.forEach(v => Object.assign(v.style, style)) :
				Object.assign(el.style, style);
		}

		return el;
	},

	/**
	 * Check and parse value to number
	 * @param {Number|String} val
	 * @param {Number} defVal
	 * @return {Number}
	 */
	getNumValue(val, defVal) {
		let num = val;

		return isNaN(num = parseInt(num, 10)) ? defVal : num;
	},

	/**
	 * Return unit formatted value
	 * @param {Number|String} val
	 * @return {String} val Value formatted with unit
	 */
	getUnitValue(val) {
		const rx = /(?:[a-z]{2,}|%)$/;

		return (parseInt(val, 10) || 0) + (String(val).match(rx) || "px");
	},

	/**
	 * Get element's outerHeight value with margin
	 * @param {HTMLElement} el
	 * @returns {number}
	 */
	outerHeight(el) {
		let height = el.offsetHeight;
		const style = getComputedStyle(el);

		height += parseInt(style.marginTop, 10) + parseInt(style.marginBottom, 10);

		return height;
	},

	/**
	 * Checks whether hardware acceleration is enabled.
	 *
	 * @ko       
	 * @method eg#isHWAccelerable
	 * @return {Boolean} Indicates whether hardware acceleration is enabled. <ko>    </ko>
	 * @example
	 eg.isHWAccelerable();  // Returns 'true' when hardware acceleration is supported

	 // also, you can control return value
	 eg.hook.isHWAccelerable = function(defalutVal,agent) {
if(agent.os.name === "ios") {
	// if os is 'ios', return value is 'false'
	return false;
} else if(agent.browser.name === "chrome" ) {
	// if browser is 'chrome', return value is 'true'
	return true;
}
return defaultVal;
}
	 */
	isHWAccelerable() {
		const ua = window.navigator.userAgent;
		let result = false;
		let match;

		// chrome 25- has a text blur bug (except Samsung's sbrowser)
		if ((match = ua.match(/Chrome\/(\d+)/))) {
			result = match[1] >= "25";
		} else if (/ie|edge|firefox|safari|inapp/.test(ua)) {
			result = true;
		} else if ((match = ua.match(/Android ([\d.]+)/))) {
			const version = match[1];
			const useragent = (ua.match(/\(.*\)/) || [null])[0];

			// android 4.1+ blacklist
			// EK-GN120 : Galaxy Camera, SM-G386F : Galaxy Core LTE
			// SHW-M420 : Galaxy Nexus , SHW-M200 : NexusS , GT-S7562 : Galaxy S duos
			result = version >= "4.1.0" && !/EK-GN120|SM-G386F/.test(useragent) ||
				version >= "4.0.3" &&
				/SHW-|SHV-|GT-|SCH-|SGH-|SPH-|LG-F160|LG-F100|LG-F180|LG-F200|EK-|IM-A|LG-F240|LG-F260/.test(useragent) &&
				!/SHW-M420|SHW-M200|GT-S7562/.test(useragent);
		}

		this.isHWAccelerable = () => result;
		return result;
	},

	/**
	 * Returns the syntax of the translate style which is applied to CSS transition properties.
	 *
	 * @ko CSS    translate   
	 * @method eg#translate
	 * @param {String} x Distance to move along the X axis <ko>x   </ko>
	 * @param {String} y Distance to move along the Y axis <ko>y   </ko>
	 * @param {Boolean} [isHA] Force hardware acceleration <ko>   </ko>
	 * @return {String} Syntax of the translate style <ko>translate  </ko>
	 * @example
	 eg.translate('10px', '200%');  // translate(10px,200%);
	 eg.translate('10px', '200%', true);  // translate3d(10px,200%,0);
	 */
	translate(x, y, isHA) {
		return isHA || false ?
			`translate3d(${x},${y},0)` : `translate(${x},${y})`;
	},

	// 1. user press one position on screen.
	// 2. user moves to the other position on screen.
	// 3. when user releases fingers on screen, 'click' event is fired at previous position.
	hasClickBug() {
		const ua = window.navigator.userAgent;
		const result = /Safari/.test(ua);

		this.hasClickBug = () => result;
		return result;
	}
};

class MixinBuilder {
	constructor(superclass) {
		this.superclass = superclass || class {};
	}

	with(...mixins) {
		return mixins.reduce((c, m) => m(c), this.superclass);
	}
}

const Mixin = superclass => new MixinBuilder(superclass);

export {utils, Mixin};



